import React from "react";
import "./List.css";
const List = ({ data, isColumn }) => {
  if (!data) return null; // Handle case when no data is provided

  return (
    <div className={`d-flex ${isColumn ? "flex-column" : "flex-row"}`}>
      <h5 className={`mb-1 text-start title ${!isColumn ? "me-2 pt-2" : ""}`}>
        {data.title}
      </h5>
      <ul
        className={`list-group d-flex flex-row justify-content-between ${
          !isColumn ? "w-75" : ""
        }`}
      >
        <li
          className={`list-group-item flex-grow-1 text-start mx-0 ps-0 ${
            !isColumn && "d-flex flex-row justify-content-around"
          }`}
          style={{ border: "0" }}
        >
          <p>
            <b>Type:</b> {data.type}
          </p>
          <p>
            <b>Owner:</b> {data.owner}
          </p>
          <p>
            <b>CAD Format:</b> {data.cadFormat}
          </p>
        </li>
        {isColumn && (
          <>
            <li
              className={`list-group-item flex-grow-1 text-start mx-1 ${
                !isColumn && "d-flex flex-row"
              }`}
              style={{ border: "0", paddingLeft: "0" }}
            >
              <p>
                <b>Revision:</b> {data.revision}
              </p>

              <p>
                <b>Collabspace:</b> {data.collabspace}
              </p>
              <p>
                <b>Maturity State:</b> {data.maturityState}
              </p>
            </li>
            <li
              className={`list-group-item flex-grow-1 text-start mx-1 ${
                !isColumn && "d-flex flex-row"
              }`}
              style={{ border: "0", paddingLeft: "0" }}
            >
              <p>
                <b>EIN:</b> {data.enterpriseItemNumber}
              </p>
            </li>
            {/* Vertical row */}
            <div className="d-flex">
              <li className="vr mb-3"></li>
            </div>
            <li
              className={`list-group-item flex-grow-1 text-start mx-1 ${
                !isColumn && "d-flex flex-row"
              }`}
              style={{ border: "0" }}
            >
              <p className="description">{data.description}</p>
            </li>
          </>
        )}
      </ul>
    </div>
  );
};

export default List;
