import React, { useEffect } from "react";
import useDroppableArea from "../../hooks/useDroppableArea";
import Loader from "../Loader/Loader"; // Import the Loader
import "./DragAndDrop.css"; // Import styles for the component
import { Image } from "react-bootstrap";

const DragAndDropComponent = () => {
  const { initializeDroppableArea, loading, isDropped  } = useDroppableArea();
  // Initialize droppable area on mount
  useEffect(() => {
    if (!isDropped) {
        initializeDroppableArea();
    }
}, [isDropped]);

  return (
    <>
      {loading && (
        <Loader />
      )}
      {!loading && (
        <div
          className={`droppable-container mt-4 ${isDropped ? "dropped-active" : ""}`}
        >
          <Image
            style={{ width: "90px", height: "90px" }}
            src="https://thewhitechamaleon.github.io/testrapp/images/drag.png"
            alt="Data Collect"
            className="search-icon"
          />
          <span className="drag-and-drop-text">Drag and Drop</span>
          <div className="divider-container">
            <hr className="divider" />
            <span className="divider-text">or</span>
            <hr className="divider" />
          </div>
          <div className="search-content-container">
            <Image style={{ width: "40px", height: "40px" }} src="https://thewhitechamaleon.github.io/testrapp/images/search.png" alt="Search" />
            <span className="ms-4 drag-and-drop-text">
              Click here to search content
            </span>
          </div>
        </div>
      )}
    </>
  );
};

export default DragAndDropComponent;


