import "bootstrap/dist/css/bootstrap.min.css";
import RevisionFloat from "./pages/revisionFloat";
import { useEffect } from "react";
import useDroppableArea from "./hooks/useDroppableArea";
import "./App.css";
import "./styles/variables.css";
import PlantAssignment from "./pages/PlantAssignment/plantAssignment";

const App = ({ setGlobalHandleDrop }) => {
  const { handleDrop } = useDroppableArea();

  useEffect(() => {
    if (handleDrop) {
      console.log("[App] Providing handleDrop to global scope...");
      setGlobalHandleDrop(handleDrop); // Pass handleDrop to the global variable
    } else {
      console.error("[App] handleDrop is undefined.");
    }
  }, [handleDrop, setGlobalHandleDrop]);

  // returned the plantAssignment page.
  return <PlantAssignment />;
  //  return <RevisionFloat />;
};

export default App;
