import { setChildPlantDetails } from "../../store/plantAssignmentSlice";
import { loadWAFData } from "../../utils/helpers";
import { fetchAssignedPlants } from "./assignedPlantService";

export const fetchLibraryData = async (
  libraryId,
  headers,
  dispatch,
  objectID
) => {
  try {
    console.log(
      "[Library Id Service] Fetching data for library ID:",
      libraryId
    );

    const libraryDataURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslib/dslib:Library/${libraryId}?$mask=dslib:ExpandClassifiableClassesMask`;

    const WAFData = await loadWAFData();

    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(libraryDataURL, {
        method: "GET",
        headers,
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    console.log("[Library Data Service] Data received:", response);

    const { member } = response;
    if (member && member.length > 0) {
      console.log("[Library Data Service] Member found:", member);

      const factoriesData = member[0]?.ChildClasses;
      if (
        factoriesData &&
        factoriesData.member &&
        factoriesData.member.length > 0
      ) {
        console.log(
          "[Library Data Service] Factories Data Members:",
          factoriesData.member
        );
        const plantsData = factoriesData.member[0]?.ChildClasses;
        if (plantsData && plantsData.member && plantsData.member.length > 0) {
          console.log(
            "[Library Data Service] Plant Data Members:",
            plantsData.member
          );
          if (dispatch) {
            dispatch(setChildPlantDetails(plantsData.member));
            console.log("[Redux] Plant Data dispatched:", plantsData.member);
            fetchAssignedPlants(dispatch, plantsData.member, headers, objectID);
          }
        } else {
          console.warn("[Library Data Service] No Plant Data found.");
        }
      } else {
        console.warn("[Library Data Service] No factories data members found.");
      }
    } else {
      console.warn("[Library Data Service] No member found in the response.");
    }

    return;
  } catch (error) {
    console.error("[Library Data Service] Failed to fetch data:", error);
    throw error;
  }
};
