import { createSlice } from '@reduxjs/toolkit';
const initialState = {
  droppedObjectData: {
    draggedData: {}, // Dragged object details
    versions: [], // Object versions (revisions)
    parentDetails: [], // Parent metadata
  },
  loadingParentDetails: false, // Loading state for parent metadata
  // csrfHeaders: null, // Add csrfHeaders here
  loadingRefresh: false, // Add loading state for refresh
};

const droppedObjectSlice = createSlice({
  name: "droppedObject",
  initialState,
  reducers: {
    setDroppedObjectData: (state, action) => {
      state.droppedObjectData = {
        ...state.droppedObjectData,
        ...action.payload,
      };
    },
    setParentDetailsLoading: (state, action) => {
      state.loadingParentDetails = action.payload;
    },
    setLoadingRefresh: (state, action) => {
      state.loadingRefresh = action.payload;
    },
    // setCsrfHeadersAction(state, action) { // Action to update CSRF headers
    //   state.csrfHeaders = action.payload;
    // },
  },
});



export const { setDroppedObjectData, setParentDetailsLoading, setLoadingRefresh  } = droppedObjectSlice.actions;
export default droppedObjectSlice.reducer;
