import React from "react";
import { useSelector } from "react-redux";
import DragAndDropComponent from "../../components/DragAndDrop/DragAndDrop";
import CardComponent from "../../components/Card/Card";
import Table from "../../components/Table/Table";
import { Image } from "react-bootstrap";
import ".././revisionFloat.css";
import Loader from "../../components/Loader/Loader";
import { tab } from "@testing-library/user-event/dist/tab";
import AddPlantButton from "../../components/AddPlantButton/AddPlantButton";

const PlantAssignment = () => {
  // Access Redux store
  const droppedObjectData = useSelector(
    (state) => state.plantAssignment.droppedObjectData
  );

  // const childPlantDetails = useSelector(
  //   (state) => state.plantAssignment.childPlantDetails
  // );

  // const assignedPlantDetails = useSelector(
  //   (state) => state.plantAssignment.assignedPlantDetails
  // );

  // Data for Available Plants
  const uniquePlantDetails = useSelector(
    (state) => state.plantAssignment.uniquePlantDetails
  );
  // Dta for Assigned Plants
  const assignedPlantAttributes = useSelector(
    (state) => state.plantAssignment.assignedPlantAttributes
  );
  // const loadingParentDetails = useSelector(
  //   (state) => state.droppedObject.loadingParentDetails
  // );
  // const loadingRefresh = useSelector(
  //   (state) => state.droppedObject.loadingRefresh
  // ); // New state

  const loadingRefresh = false;
  const isTableLoading = true;

  // console.log("[Plant Assignment] Child Plant Details: ", childPlantDetails);
  console.log("droppedObjectData", droppedObjectData);
  // console.log(
  //   "[Plant Assignment] Assigned Plant Details: ",
  //   assignedPlantDetails
  // );
  console.log("[Plant Assignment] Unique Plant Details: ", uniquePlantDetails);
  console.log(
    "[Plant Assignment] Assigned Plant Attributes",
    assignedPlantAttributes
  );

  // Process table data
  // const tableData = React.useMemo(() => {
  //   if (!droppedObjectData || !droppedObjectData.parentDetails) return [];
  //   return droppedObjectData.parentDetails.map((parent) => ({
  //     EIN:
  //       parent.parentMetadata?.["dseng:EnterpriseReference"]?.partNumber ||
  //       "N/A",
  //     Title: parent.parentMetadata?.title || "N/A",
  //     Description: parent.parentMetadata?.description || "N/A",
  //     Type: parent.parentMetadata?.type || "N/A",
  //     Revision: parent.parentMetadata?.revision || "N/A",
  //     "Connected Child Revision": parent.connectedChildRevision || "N/A",
  //     "Latest child connected": "N/A", // Placeholder for additional logic
  //     "To-Be child connected": "N/A", // Placeholder for additional logic
  //     State: parent.parentMetadata?.state || "N/A",
  //     Owner: parent.parentMetadata?.owner || "N/A",
  //     "CAD Format": "3DExperience", // Static value
  //     Collabspace: parent.parentMetadata?.collabspace || "N/A",
  //   }));
  // }, [droppedObjectData]);

  // process unique plant table data in Plant Assignment
  const tableData = React.useMemo(() => {
    if (uniquePlantDetails.length < 1) return [];
    const mappedData = uniquePlantDetails.map((plant) => {
      console.log("Mapping plant:", plant);
      return {
        "Available Plant": plant?.title || "N/A",
      };
    });
    console.log("Mapped Data:", mappedData);
    return mappedData;
  }, [uniquePlantDetails]);

  //  process assigned plant data in Plant Assignment
  const assignedTableData = React.useMemo(() => {
    if (
      !Array.isArray(assignedPlantAttributes) ||
      assignedPlantAttributes.length < 1
    ) {
      return [];
    }

    const mappedData = assignedPlantAttributes.map((plant) => {
      console.log("Mapping plant:", plant);
      return {
        Plant: plant?.title || "N/A",
      };
    });

    console.log("Mapped Data:", mappedData);
    return mappedData;
  }, [assignedPlantAttributes]);
  // Process card data
  const cardData = React.useMemo(() => {
    if (!droppedObjectData || !droppedObjectData.draggedData?.member?.[0])
      return null;
    const item = droppedObjectData.draggedData.member[0];
    return {
      title: item.title || "N/A",
      type: item.type || "N/A",
      revision: item.revision || "N/A",
      maturityState: item.state || "N/A",
      owner: item.owner || "N/A",
      collabspace: item.collabspace || "N/A",
      enterpriseItemNumber:
        item["dseng:EnterpriseReference"]?.partNumber || "N/A",
      cadFormat: "3DExperience", // Static value
      description: item.description || "No Description Available",
    };
  }, [droppedObjectData]);

  // Define columns for the table
  // const columns = React.useMemo(
  //   () => [
  //     { accessorKey: "EIN", header: "EIN" },
  //     { accessorKey: "Title", header: "Title" },
  //     { accessorKey: "Description", header: "Description" },
  //     { accessorKey: "Type", header: "Type" },
  //     { accessorKey: "Revision", header: "Revision" },
  //     {
  //       accessorKey: "Connected Child Revision",
  //       header: "Connected Child Revision",
  //     },
  //     {
  //       accessorKey: "Latest child connected",
  //       header: "Latest child connected",
  //     },
  //     { accessorKey: "To-Be child connected", header: "To-Be child connected" },
  //     { accessorKey: "State", header: "State" },
  //     { accessorKey: "Owner", header: "Owner" },
  //     { accessorKey: "CAD Format", header: "CAD Format" },
  //     { accessorKey: "Collabspace", header: "Collabspace" },
  //   ],
  //   []
  // );

  // Define columns for the table in Plant Assignment
  const columns = React.useMemo(
    () => [{ accessorKey: "Available Plant", header: "Available Plant" }],
    []
  );
  const assignedColumns = React.useMemo(
    () => [
      { accessorKey: "Plant", header: "Plant" },
      { accessorKey: "Seq", header: "Seq" },
      { accessorKey: "Status", header: "Status" },
      { accessorKey: "MFG Change", header: "MFG Change" },
      { accessorKey: "MFG Status", header: "MFG Status" },
      { accessorKey: "Change", header: "Change" },
      { accessorKey: "Change Status", header: "Change Status" },
      { accessorKey: "Oracle Template", header: "Oracle Template" },
      { accessorKey: "ERP Status", header: "ERP Status" },
      { accessorKey: "ERP Export", header: "ERP Export" },
      { accessorKey: "Lead Plant", header: "MBom" },
      { accessorKey: "Sort Value", header: "Sort Value" },
    ],
    []
  );

  console.log(tableData);
  console.log(uniquePlantDetails);
  return (
    <>
      {/* Conditionally show DragAndDropComponent */}
      {(!droppedObjectData.draggedData ||
        !droppedObjectData.draggedData.member?.[0]?.id) && (
        <DragAndDropComponent />
      )}
      {loadingRefresh ? (
        <Loader /> // Show loader while refreshing
      ) : (
        <div className="content-wrapper p-3">
          <div className="d-flex border-bottom">
            <div className="ms-2 p-0 pt-4">
              <Image
                src="https://thewhitechamaleon.github.io/testrapp/images/home.png"
                alt="home-icon"
                className="home-icon"
              />
            </div>
            {/* Display cards only if there is data */}
            {cardData && <CardComponent data={cardData} />}
          </div>
          {/* {loadingParentDetails && <Loader />} */}
          {/* Display table only if there is data */}
          {/* {!loadingParentDetails && tableData.length > 0 && (
            <Table data={tableData} columns={columns} />
          )} */}

          <Table data={tableData} columns={columns} isTableLoading></Table>
          <AddPlantButton plants={uniquePlantDetails} />

          {/* <Table
            data={assignedTableData}
            columns={assignedColumns}
            isTableLoading
          ></Table> */}
        </div>
      )}
    </>
  );
};

export default PlantAssignment;
