import { setLoadingRefresh } from "../../store/droppedObjectSlice";

export const refreshWidgetData = async (droppedData, handleDrop, dispatch) => {
    console.log("[Refresh] Re-triggering fetch logic...");
    console.log("[Refresh] Dropped data:", droppedData);
    if (!droppedData || droppedData.length === 0) {
        console.warn("[Refresh] No dropped data available to refresh.");
        dispatch(setLoadingRefresh(false));
        return;
    }
    try {
        dispatch(setLoadingRefresh(true));
        const dataItems = droppedData.map((item) => ({
            objectId: item.id,
        }));
        console.log("[Refresh] Prepared dataItems for handleDrop:", dataItems);

        if (typeof handleDrop !== "function") {
            throw new Error("[Refresh] handleDrop is not a function.");
        }
        await handleDrop(dataItems);
        console.log("[Refresh] handleDrop completed.");                   

    } catch (error) {
        console.error("[Refresh] Error during handleDrop execution:", error);
        dispatch(setLoadingRefresh(false));
    } finally {
        console.log("[Refresh] Setting loading to false.");
        dispatch(setLoadingRefresh(false));
    }
};
