import {
  setAssignedPlantAttributes,
  setAssignedPlantDetails,
  setUniquePlantDetails,
} from "../../store/plantAssignmentSlice";
import { loadWAFData } from "../../utils/helpers";

export const fetchAssignedPlants = async (
  dispatch,
  plantMember,
  headers,
  objectID
) => {
  try {
    console.log("[Assigned Plant Service] Plant Members:", plantMember);

    const libraryDataURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslib/dslib:ClassifiedItem/${objectID}?$mask=dslib:ClassificationAttributesMask`;

    const WAFData = await loadWAFData();

    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(libraryDataURL, {
        method: "GET",
        headers,
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    console.log("[Assigned Plant Service] Data received:", response);

    const classIds = response.member.flatMap((memberItem) =>
      memberItem?.ClassificationAttributes?.member.map((item) => item?.ClassID)
    );
    const plantIds = plantMember.map((plant) => plant?.id);
    console.log(plantIds);

    const assignedPlants = classIds.filter(
      (classId) => plantIds.includes(classId) // Check if ClassID matches any plant's id
    );

    const assignedPlantsAttributes = response.member.flatMap((memberItem) =>
      memberItem?.ClassificationAttributes?.member.filter(
        (attribute) => assignedPlants.includes(attribute?.ClassID) // Match ClassID with assignedPlants
      )
    );
    console.log(
      "[Assigned Plant Service] Assigned Plant Attribute",
      assignedPlantsAttributes
    );

    const updatedAssignedPlantsAttributes = assignedPlantsAttributes.map(
      (attributeObj) => {
        // Transform the Attributes array
        const updatedAttributes = attributeObj.Attributes.map((attr) => {
          // Remove the first three characters from the value of 'e'
          return {
            ...attr,
            name: attr.name.substring(3), // Update 'e' value only if it starts with 'MVO'
          };
        });

        // Return the updated object with transformed Attributes
        return {
          ...attributeObj, // Preserve other properties of the object
          Attributes: updatedAttributes, // Update the Attributes with transformed 'e' values
        };
      }
    );
    console.log(
      "Updated Assigned Plants Attributes:",
      updatedAssignedPlantsAttributes
    );

    const attributes = assignedPlantsAttributes.Attributes;
    console.log(attributes);
    const assignedPlantsDetails = plantMember.filter((plant) =>
      assignedPlants.includes(plant.id)
    );

    const uniquePlants = plantIds.filter(
      (plantId) => !assignedPlants.includes(plantId)
    ); // Plants that are not in assignedPlants

    const uniquePlantsDetails = plantMember.filter((plant) =>
      uniquePlants.includes(plant.id)
    );
    console.log("Assigned Plants:", assignedPlants);
    console.log("Unique Plants:", uniquePlants);
    console.log(
      "[Assigned Plant Service] AssignedPlantDetails: ",
      assignedPlantsDetails
    );
    console.log(
      "[Assigned Plant Service] UniquePlantDetails: ",
      uniquePlantsDetails
    );

    const assignedAttributes = updatedAssignedPlantsAttributes.map((item2) => {
      const matchItem = assignedPlantsDetails.find(
        (item1) => item1.id === item2.ClassID
      );

      if (matchItem) {
        return {
          ...item2,
          title: matchItem.title,
        };
      }
      return item2;
    });

    console.log(
      "[Assigned Plant Service] Merged Assignd Attributes",
      assignedAttributes
    );
    // to store the Assigned Plant Details in Redux.
    if (dispatch && assignedPlants) {
      dispatch(setAssignedPlantDetails(assignedPlantsDetails));
      console.log("[Redux] Assigned Plant Details: ", assignedPlantsDetails);
    }
    // to store unique plant details in Redux.
    if ((dispatch, uniquePlantsDetails)) {
      dispatch(setUniquePlantDetails(uniquePlantsDetails));
      console.log("[Redux] Unique Plant Details: ", uniquePlantsDetails);
    }
    if (dispatch && assignedAttributes) {
      dispatch(setAssignedPlantAttributes(assignedAttributes));
      console.log("[Redux] Assigned plant Attributes: ", assignedAttributes);
    }

    if (classIds && classIds.length > 0) {
      console.log("Class IDs:", classIds);
    } else {
      console.warn("No Class IDs found.");
    }
    return;
  } catch (error) {
    console.error("[Library Data Service] Failed to fetch data:", error);
    throw error;
  }
};
