import {
  setCollabSpaceData,
  setLibraryId,
} from "../../store/plantAssignmentSlice";
import { loadWAFData } from "../../utils/helpers";
import { fetchLibraryData } from "./libraryIdService";

export const fetchCollabspaceData = async (
  collabSpace,
  headers,
  dispatch,
  objectID
) => {
  try {
    console.log(
      "[Collabspace Service] Fetching data for collabspace:",
      collabSpace
    );

    const collabspaceURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslib/dslib:Library/search?$searchStr=${collabSpace}`;

    const WAFData = await loadWAFData();

    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(collabspaceURL, {
        method: "GET",
        headers,
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    console.log("[Collabspace Service] Data received:", response);
    const collabData = {
      member: response.member,
      totalItems: response.totalItems,
      nlsLabel: response.nlsLabel,
    };

    // Find the member where title matches the collabSpace
    const matchingCollabData = collabData.member?.find(
      (item) => item.title === collabSpace
    );

    // to store the collabData in Redux Slice.
    if (dispatch) {
      dispatch(setCollabSpaceData({ collabData }));
      console.log("[Redux] CollabData Stored: ", collabData);
    }
    // to store the LibraryId in Redux Slice.
    if (dispatch && matchingCollabData) {
      dispatch(setLibraryId(matchingCollabData.id));
      console.log("[Redux] Library ID: ", matchingCollabData.id);
      fetchLibraryData(matchingCollabData.id, headers, dispatch, objectID);
    } else {
      console.warn(
        "[Matching Member] No member found with the title matching:",
        collabSpace
      );
    }

    return response;
  } catch (error) {
    console.error("[Collabspace Service] Failed to fetch data:", error);
    throw error; // Re-throw the error to handle it in the calling function
  }
};
