import React, { useEffect, useMemo, useState } from "react";
import {
  useReactTable,
  flexRender,
  getCoreRowModel,
  getSortedRowModel,
} from "@tanstack/react-table";
import { unparse } from "papaparse"; // For CSV export
import "./Table.css";
import Loader from "../Loader/Loader";

const ReusableTable = ({ columns, data, editable = false, isTableLoading }) => {
  const [tableData, setTableData] = useState(data || []);
  const [rowSelection, setRowSelection] = useState({});

  useEffect(() => {
    if (data) {
      setTableData(data);
    }
  }, [data]);

  const handleEditCell = (rowIndex, columnId, value) => {
    const updatedData = [...tableData];
    updatedData[rowIndex][columnId] = value;
    setTableData(updatedData);
  };
  console.log("[Table] Data", data);
  console.log("[Table] Table Data: ", tableData);

  // Enhance columns for editable functionality and add checkbox selection
  const enhancedColumns = useMemo(() => {
    const selectionColumn = {
      id: "selection",
      header: ({ table }) => (
        <input
          type="checkbox"
          ref={(el) => {
            if (el) {
              el.indeterminate = table.getIsSomeRowsSelected();
            }
          }}
          checked={table.getIsAllRowsSelected()}
          onChange={table.getToggleAllRowsSelectedHandler()}
        />
      ),
      cell: ({ row }) => (
        <input
          type="checkbox"
          {...{
            checked: row.getIsSelected(),
            onChange: row.getToggleSelectedHandler(),
          }}
        />
      ),
    };

    const baseColumns = editable
      ? columns.map((column) => ({
          ...column,
          cell: column.editable
            ? ({ row, getValue }) => (
                <input
                  className="input-cell"
                  value={getValue()}
                  onChange={(e) =>
                    handleEditCell(
                      row.index,
                      column.accessorKey,
                      e.target.value
                    )
                  }
                />
              )
            : column.cell,
        }))
      : columns;

    return [selectionColumn, ...baseColumns];
  }, [columns, editable]);

  const table = useReactTable({
    data: tableData,
    columns: enhancedColumns,
    state: {
      rowSelection: rowSelection,
    },
    onRowSelectionChange: setRowSelection,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(),
    enableRowSelection: true,
    defaultColumn: {
      size: 159,
      minSize: 45,
      maxSize: 500,
    },
    columnResizeMode: "onChange",
  });

  const columnSizeVars = useMemo(() => {
    const headers = table.getFlatHeaders();
    const colSizes = {};
    for (let i = 0; i < headers.length; i++) {
      const header = headers[i];
      colSizes[`--header-${header.id}-size`] = header.getSize();
      colSizes[`--col-${header.column.id}-size`] = header.column.getSize();
    }
    return colSizes;
  }, [table.getState().columnSizingInfo, table.getState().columnSizing]);

  const handleExportCSV = () => {
    const csv = unparse(tableData); // Convert JSON to CSV
    const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.setAttribute("download", "table-data.csv");
    link.click();
  };

  return (
    <>
      <div className="toolkit my-3">
        <div className="d-flex justify-content-between">
          <div className="d-flex">
            <button
              // onClick={""}
              className="btn btn-outline-secondary btn-lg m-2"
            >
              Replace
            </button>
            <button
              // onClick={""}
              className="btn btn-outline-secondary btn-lg m-2"
            >
              Select items in my Collaborative Space
            </button>
          </div>

          <div className="d-flex">
            <button
              onClick={() =>
                table.getToggleAllRowsSelectedHandler()({
                  target: { checked: true },
                })
              }
              className="btn btn-outline-primary btn-lg m-2"
            >
              Select All Rows
            </button>
            <button
              onClick={() =>
                table.getToggleAllRowsSelectedHandler()({
                  target: { checked: false },
                })
              }
              className="btn btn-outline-secondary btn-lg m-2"
            >
              Deselect All Rows
            </button>
            <button
              onClick={handleExportCSV}
              className="btn btn-outline-success btn-lg border-bottom-10px m-2"
            >
              Export to CSV
            </button>
          </div>
        </div>
      </div>
      <div className="table-info">
        <p>{tableData.length} Items</p>
      </div>
      <div className="table-responsive">
        <table
          className="table custom-table table-hover"
          style={{
            ...columnSizeVars,
            width: table.getTotalSize(),
          }}
        >
          <thead>
            {table.getHeaderGroups().map((headerGroup) => (
              <tr key={headerGroup.id}>
                {headerGroup.headers.map((header) => (
                  <th
                    key={header.id}
                    onClick={header.column.getToggleSortingHandler()}
                    style={{
                      width: `calc(var(--header-${header.id}-size) * 1px)`,
                    }}
                  >
                    {header.isPlaceholder
                      ? null
                      : flexRender(
                          header.column.columnDef.header,
                          header.getContext()
                        )}
                    {header.column.getIsSorted() === "asc" ? (
                      <span className="icon">↑</span>
                    ) : header.column.getIsSorted() === "desc" ? (
                      <span className="icon">↓</span>
                    ) : (
                      ""
                    )}
                    <div
                      {...{
                        onDoubleClick: () => header.column.resetSize(),
                        onMouseDown: header.getResizeHandler(),
                        onTouchStart: header.getResizeHandler(),
                        className: `resizer ${
                          header.column.getIsResizing() ? "isResizing" : ""
                        }`,
                      }}
                    />
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody>
            {!isTableLoading ? (
              <tr>
                <td
                  colSpan={table.getHeaderGroups()[0].headers.length}
                  className="loading-indicator"
                >
                  <Loader />
                </td>
              </tr>
            ) : table.getRowModel().rows.length > 0 ? (
              table.getRowModel().rows.map((row) => (
                <tr
                  key={row.id}
                  style={{
                    backgroundColor: rowSelection[row.id]
                      ? "#d5e8f2"
                      : "inherit",
                  }}
                >
                  {row.getVisibleCells().map((cell) => (
                    <td
                      key={cell.id}
                      style={{
                        width: `calc(var(--col-${cell.column.id}-size) * 1px)`,
                      }}
                    >
                      {flexRender(
                        cell.column.columnDef.cell,
                        cell.getContext()
                      )}
                    </td>
                  ))}
                </tr>
              ))
            ) : (
              <tr>
                <td
                  colSpan={table.getHeaderGroups()[0].headers.length}
                  className="no-data p-2"
                >
                  No Data Available
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </>
  );
};

export default ReusableTable;
