import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  droppedObjectData: {
    draggedData: {}, // Dragged object details
  },
  collabSpaceData: {
    collabData: {},
  },
  childPlantDetails: [], // Data for the table component
  libraryId: null, // Library ID from intermediate API call
  assignedPlantDetails: [],
  assignedPlantAttributes: {},
  uniquePlantDetails: [],
  loadingCardDetails: false, // Loading state for card data
  loadingChildPlants: false, // Loading state for child plants
  error: null, // Error state for any step in the process
};

const plantAssignmentSlice = createSlice({
  name: "plantAssignment",
  initialState,
  reducers: {
    setDroppedObjectData: (state, action) => {
      state.droppedObjectData = {
        ...state.droppedObjectData,
        ...action.payload,
      };
    },
    setCollabSpaceData: (state, action) => {
      state.collabSpaceData = {
        ...state.collabSpaceData,
        ...action.payload,
      };
    },
    setLibraryId: (state, action) => {
      state.libraryId = action.payload;
    },
    setChildPlantDetails: (state, action) => {
      state.childPlantDetails = action.payload;
    },
    setAssignedPlantDetails: (state, action) => {
      state.assignedPlantDetails = action.payload;
    },
    setUniquePlantDetails: (state, action) => {
      state.uniquePlantDetails = action.payload;
    },
    setAssignedPlantAttributes: (state, action) => {
      state.assignedPlantAttributes = action.payload;
    },
    setLoadingCardDetails: (state, action) => {
      state.loadingCardDetails = action.payload;
    },
    setLoadingChildPlants: (state, action) => {
      state.loadingChildPlants = action.payload;
    },
    setError: (state, action) => {
      state.error = action.payload;
    },
    clearState: (state) => {
      state.droppedObjectData = { draggedData: {} };
      state.cardDetails = null;
      state.libraryId = null;
      state.childPlantDetails = [];
      state.loadingCardDetails = false;
      state.loadingChildPlants = false;
      state.error = null;
    },
  },
});

export const {
  setDroppedObjectData,
  setCollabSpaceData,
  setLibraryId,
  setChildPlantDetails,
  setAssignedPlantDetails,
  setUniquePlantDetails,
  setAssignedPlantAttributes,
  setLoadingCardDetails,
  setLoadingChildPlants,
  setError,
  clearState,
} = plantAssignmentSlice.actions;

export default plantAssignmentSlice.reducer;
