import React, { useState } from "react";
import "./Card.css";
import "bootstrap/dist/css/bootstrap.css";
import { Image, Card, CardBody } from "react-bootstrap";
import List from "../List/List";
// import { IoMdArrowDropdown } from "react-icons/io";

const CardComponent = ({ data }) => {
  const [isColumn, setIsColumn] = useState(true);

  const toggleFlexDirection = () => {
    setIsColumn(!isColumn);
  };

  return (
    <Card
      className="d-flex flex-row my-4 border-0 "
      style={{ width: "100%" }}
    >
      <div className="pb-3">
        <Card.Img
          className={`${!isColumn ? "image-resize" : ""}`}
          src={
            "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/snresources/images/icons/large/I_VPMNavProduct108x144.png"
          }
        />
      </div>
      <CardBody className="p-0">
        <List data={data} isColumn={isColumn} />
      </CardBody>
      <button
       
        className="me-2 p-0 btn btn-link accordion-toggle"
        onClick={toggleFlexDirection}
      >
        <Image
          src="https://jatin2024.github.io/revision-float/images/arrow.png"
          alt="downArrow"
          className={`rotatable ${isColumn ? "rotated" : ""}`}
        />
      </button>
    </Card>
  );
};

export default CardComponent;
